<template>
  <div id="mainDiv">
    <banner></banner>
    <div id="containDiv">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import Banner from "../../components/Banner.vue";
import { mapState } from 'vuex'
export default {
  components: {
    Banner
  },
  data() {
    return {
    };
  },
  created(){
    self = this;
  },
  computed:{
    ...mapState({
      pageName: state => state.page.pageName,
      currentUser: state => state.user.user,
      loginDate: state => state.user.loginDate,
    })
  },
};
</script>

<style scoped>
#mainDiv {
  background: #1b225b;
  width: 1920px;
  height: 1086px;
}

#containDiv {
  background-color: #cddde8;
  width: 1900px;
  height: 1020px;
  margin-left: 10px;
  margin-top: 10px;
}
</style>


