<template>
  <div>
    <div class="fraction">
      <v-table :height="320"
               :columns="fractionTableColumns"
               :table-data="fractionList"
               row-hover-color="#ee9"
               row-click-color="#F0E0FF"
               title-bg-color="#BBB8BF"
               odd-bg-color="#e0f7fa"
               even-bg-color="#b2dfdb"></v-table>
    </div>
    <div class="fraction fractionBottom">
      <mu-tabs class="mutabs" inverse :value.sync="pageIndex" text-color="#1b225b">
        <mu-tab>Fraction Details</mu-tab>
        <mu-tab>Fraction Beams</mu-tab>
      </mu-tabs>
      <div class="fractionDetails" v-if="pageIndex === 0">
        <h3 style="transform:translateX(10px); text-align:left;">Treatment Parameters</h3>
        <mu-form class="deliveryDetailsForm" :model="fractionDetailsForm" label-position="top" label-width="250px" style="margin:10px">
          <el-row>
            <el-col :span="8">
              <mu-form-item prop="fractionName" label="Fraction Name" style="text-align:left; margin-left:40px;">
                <input v-model="fractionDetailsForm.fractionName" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="8">
              <mu-form-item prop="refDosePerFraction" label="Reference Dose Per Fraction(cGy)" style="text-align:left;">
                <input v-model="fractionDetailsForm.refDosePerFraction" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="8">
              <mu-form-item prop="numOfPrescribedPaths" label="Number of Prescribed Paths" style="text-align:left;">
                <input v-model="fractionDetailsForm.numOfPrescribedPaths" disabled />
              </mu-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <mu-form-item prop="status" label="Status" style="text-align:left; margin-left:40px;">
                <input v-model="fractionDetailsForm.status" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="8">
              <mu-form-item prop="fractionMU" label="Fraction MU" style="text-align:left;">
                <input v-model="fractionDetailsForm.fractionMU" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="8">
              <mu-form-item prop="numOfPrescribedBeams" label="Number of Prescribed Beams" style="text-align:left;">
                <input v-model="fractionDetailsForm.numOfPrescribedBeams" disabled />
              </mu-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <mu-form-item prop="fractionNumber" label="Fraction Number" style="text-align:left; margin-left:40px;">
                <input v-model="fractionDetailsForm.fractionNumber" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="8">
              <mu-form-item prop="deliveredDose" label="Delivered Dose(cGy)" style="text-align:left;">
                <input v-model="fractionDetailsForm.deliveredDose" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="8">
              <mu-form-item prop="lastModified" label="Last Modified" style="text-align:left;">
                <input v-model="fractionDetailsForm.lastModified" disabled />
              </mu-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="8">
              <mu-form-item prop="makeupFractionNumber" label="Makeup Fraction Number" style="text-align:left; margin-left:40px;">
                <input v-model="fractionDetailsForm.makeupFractionNumber" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="8">
              <mu-form-item prop="deliveredMU" label="Delivered MU" style="text-align:left;">
                <input v-model="fractionDetailsForm.deliveredMU" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="8">
              <mu-form-item prop="treatmentDate" label="Treatment Date" style="text-align:left;">
                <input v-model="fractionDetailsForm.treatmentDate" disabled />
              </mu-form-item>
            </el-col>
          </el-row>
        </mu-form>
      </div>
      <div class="fractionBeams" v-if="pageIndex === 1">
        <div class="fractionPathList">
          <h3>Fraction Path List: {{ fractionPathTableName }}</h3>
          <v-table :height="200"
                   :columns="fractionPathTableColumn"
                   :table-data="beamListData"
                   row-hover-color="#ee9"
                   row-click-color="#F0E0FF"
                   title-bg-color="#BBB8BF"
                   odd-bg-color="#e0f7fa"
                   even-bg-color="#b2dfdb"></v-table>
        </div>
        <div class="beamList" style="height:200px; margin-top:10px;">
          <h3>Beam List: {{ beamListTableName }}</h3>
          <v-table :height="200"
                   :columns="beamListTableColumn"
                   :table-data="fractionPathListData"
                   row-hover-color="#ee9"
                   row-click-color="#F0E0FF"
                   title-bg-color="#BBB8BF"
                   odd-bg-color="#e0f7fa"
                   even-bg-color="#b2dfdb"></v-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        fractionTableColumns: [
          {
            title: "Fraction Name",
            field: "fractionName",
            width: 320,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Status",
            field: "status",
            width: 313,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Number of Beams",
            field: "numOfBeams",
            width: 313,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Reference Dose Per Fraction(cGy)",
            field: "refDosePerFraction",
            width: 313,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Delivered Dose(cGy)",
            field: "deliveredDose",
            width: 320,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Treatment Date",
            field: "treatmentDate",
            width: 320,
            titleAlign: "center",
            columnAlign: "center",
          }
        ],
        fractionList: [],
        selectedFraction: [],
        pageIndex: 0,
        fractionDetailsForm: {
          fractionName: 'hello world!',
          refDosePerFraction: '',
          numOfPrescribedPaths: '',
          fractionNumber: '',
          deliveredDose: '',
          lastModified: '',
          makeupFractionNumber: '',
          deliveredMU: '',
          treatmentDate: '2020-03-25',
        },
        fractionPathListData: [],
        fractionPathTableName: 'F1',
        fractionPathTableColumn: [
          {
            title: "Path Name",
            field: "pathName",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Collimator",
            field: "collimator",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Number of Beams",
            field: "numberOfBeams",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Path Dose(cGy)",
            field: "pathDose",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Delivered Dose(cGy)",
            field: "deliveredDose",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Path MU",
            field: "pathMU",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Deliverd MU",
            field: "deliverdMU",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          }
        ],
        beamListTableName: '',
        beamListTableColumn: [
          {
            title: "SID",
            field: "sId",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Beam Number",
            field: "beamNumber",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Collimator",
            field: "collimator",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "beamDose",
            field: "beamDose",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Delivered Dose(cGy)",
            field: "deliveredDose",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Beam MU",
            field: "beamMU",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Deliverd MU",
            field: "deliverdMU",
            width: 270,
            titleAlign: "center",
            columnAlign: "center",
          }
        ],
        beamListData: []
      }
    },
    methods: {
      setStyle(row, rowIndex) {
        let style = "color:green;";
        return style;
      },
      mouseup() {
        alert(row);
      }
    }
  }
</script>

<style scoped>
  .fraction {
    width: 1900px;
    height: 300px;
    background-color: #BDB9B9;
  }

  .fractionBottom {
    margin-top: 5px;
    height: 670px;
  }

  .fractionDetails, .fractionBeams {
    width: 1900px;
    height: 660px;
    background: #E4E7ED;
  }

  input {
    border: 1px solid #ccc;
    padding: 7px 0px;
    border-radius: 3px;
    padding-left: 5px;
    font-size: 16px;
    height: 25px;
    width: 300px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out 0.15s, -webkit-box-shadow ease-in-out 0.15s;
    -moz-transition: border-color ease-in-out 0.15s, -moz-box-shadow ease-in-out 0.15s;
    -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  }

  .fraction /deep/ .mu-table-header, .fraction /deep/ .mu-table th {
    height: 30px;
  }

  .fractionPathListTable /deep/ .tbodyDiv {
    max-height: 200px;
  }

  .beamListTable /deep/ .tbodyDiv {
    max-height: 320px;
  }

  .mutabs /deep/ .mu-tab .mu-tab-wrapper {
    height: 30px;
    min-height: 30px;
  }

  .mutabs /deep/ .mu-tab {
    transform: translateX(15px);
  }

  .mutabs /deep/ .mu-tab-active {
    transform: translateX(15px);
    background: border-box rgb(207, 196, 196);
    font-weight: bold;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }

  .mutabs /deep/ mu-tabs {
    background-color: rgb(180, 180, 180);
  }

  h3 {
    text-align: left;
    padding-left: 10px;
  }
</style>



