<template>
  <div id="app">
    <div>
      <router-view to="/">MainView</router-view>
    </div>
  </div>
</template>

<script>
export default {
  created: function() {
    self = this;
  },
  watch: {
    '$route' (to, from) {
      if(to.name === 'MainView'){
        self.$store.dispatch('setPageName', 'Main');
      }else if (to.name === 'PatientAdmin') {
        self.$store.dispatch('setPageName', 'Patient Admin');
      } else if (to.name === 'Importpatient') {
        self.$store.dispatch('setPageName', 'Import Patient');
      } else if(to.name === 'EditPatient'){
        self.$store.dispatch('setPageName', 'Edit Patient');
      }else if(to.name === 'PlanAdmin'){
        self.$store.dispatch('setPageName', 'Plan Admin');
      } else if(to.name === 'PlanDetails'){
        self.$store.dispatch('setPageName', 'Treatment Plan Management');
      } else if(to.name === 'UserAdmin'){
        self.$store.dispatch('setPageName', 'User Admin');
      } else if(to.name === 'AddUser'){
        self.$store.dispatch('setPageName', 'Add User');
      } else if(to.name === 'EditUser'){
        self.$store.dispatch('setPageName', 'Edit User');
      }
    }
  }
};
</script>

<style>
*{
  margin: 0px;
  padding: 0px;
}
#app {
  font-family: Helvetica, sans-serif;
  text-align: center;
  width: auto;
}
</style>



