<template>
  <div id="mainDiv">
    <banner></banner>
    <button class="submit" @mouseup="toPatientAdmin">Patient Admin</button>
    <button class="submit" @mouseup="toPlanAdmin">Plan Admin</button>
    <button class="submit">Record Admin</button>
    <button class="submit" @mouseup="toUserAdmin">User Admin</button>
  </div>
</template>

<script>
import Banner from "../../components/Banner.vue";
export default {
  components: {
    Banner
  },
  data() {
    return {
    };
  },
  created: function() {
    self = this;
  },
  methods: {
    toPatientAdmin: function() {
      self.$router.push({
        name: "PatientAdmin"
      });
    },
    toPlanAdmin: function() {
      self.$router.push({
        name: "PlanAdmin"
      });
    },
    toUserAdmin: function(){
      self.$router.push({
        name: "UserAdmin"
      });
    },
  },
};
</script>

<style scoped>
* {
  margin: 0;
  padding: 0;
}

#mainDiv {
  background: #1b225b;
  width: 1920px;
  height: 1086px;
}

.submit {
  cursor: pointer;
  width: 200px;
  height: 200px;
  font-size: 35px;
  text-align: center;
  color: #ffffff;
  background: #169bd5;
  border-radius: 15px;
  font-family: Arial, Helvetica, sans-serif;
  border: 0px;
  margin: 380px 100px 0px 0px;
  position: relative;
  vertical-align: middle;
}

.submit:hover {
  background-color: #0f6a91;
}

.submit:active {
  background-color: #095779;
  color: #d2b9b9;
}
</style>


