import axios from "axios";

axios.defaults.timeout = 5000;
axios.defaults.headers['Content-Type'] = 'application/json;charset=UTF-8';

function axiosGet(url, params = null) {
  url = _wrapUrl(url, params);
  return axiosmothod(methodType.get, url, null);
}

function axiosPut(url, params, data) {
  url = _wrapUrl(url, params);
  return axiosmothod(methodType.put, url, data);
}

function axiosPost(url, data) {
  return axiosmothod(methodType.post, url, data);
}

function axiosDelete(url, params = null) {
  url = _wrapUrl(url, params);
  return axiosmothod(methodType.delete, url, null);
}

//
async function axiosmothod(method, url, data) {
  return new Promise((resolve, reject) => {
    axios({
      method: method,
      url: url,
      data: data,
    }).then(response => {
      resolve(response.data);
    }, err => {
      reject(err);
    });
  });
}


/**
 * URLpathParams
 * @param {string} url
 * @param {object} params
 * @returns {string}
 * @private
 */
function _wrapUrl(url, params) {
  if (params !== null) {
    let matches = _getMatches(url)
    for (let match of matches) {
      let value = params[match.replace(':', '')]
      if (value !== null) {
        url = url.replace(match, value)
      }
    }
  }
  return url
}

/**
 * Group
 * @param {string} string
 * @returns {Array}
 * @private
 */
function _getMatches(string) {
  let matches = []
  let regex = /(:[a-z_A-Z]+)/
  let match = regex.exec(string)
  while (match !== null) {
    matches.push(match[1])
    string = string.replace(match[1], '')
    match = regex.exec(string)
  }
  return matches
}

var methodType = {
  get: 'get',
  put: 'put',
  post: 'post',
  delete: 'delete'
}

export default {
  axiosGet,
  axiosPut,
  axiosPost,
  axiosDelete,
}



