<template>
  <div id="formDiv">
    <mu-form :model="addUserForm" class="mu-demo-form" :label-position="labelPosition" label-width="150px">
      <el-row>
        <el-col :span="12">
          <mu-form-item prop="userId" label="User Id" style="text-align:right;" :rules="textFieldRules">
            <mu-text-field v-model="addUserForm.userId"></mu-text-field>
          </mu-form-item>
          <mu-form-item prop="userName" label="User Name" style="text-align:right;" :rules="textFieldRules">
            <mu-text-field v-model="addUserForm.userName"></mu-text-field>
          </mu-form-item>
          <mu-form-item prop="sex" label="Gender" style="text-align:right;">
            <mu-select v-model="addUserForm.sex">
              <mu-option v-for="option, index in gender" :key="option" :label="option" :value="option"></mu-option>
            </mu-select>
          </mu-form-item>
          <mu-form-item prop="userType" label="User Type" style="text-align:right;" :rules="textFieldRules">
            <mu-select v-model="addUserForm.userType">
              <mu-option v-for="option, index in userType" :key="option" :label="option" :value="option"></mu-option>
            </mu-select>
          </mu-form-item>
        </el-col>
        <el-col :span="12">
          <mu-form-item prop="privilege" label="Privilege" style="text-align:right;" :rules="textFieldRules">
            <mu-select v-model="addUserForm.privilege">
              <ul>
                <li>
                  <mu-checkbox label="All" v-model="checkAll" @change="handleCheckAll"></mu-checkbox>
                </li>
                <li v-for="i in checkboxName">
                  <mu-checkbox :value="i" v-model="addUserForm.privilege" :label='i'></mu-checkbox>
                </li>
              </ul>
            </mu-select>
          </mu-form-item>
          <mu-form-item prop="status" label="Status" style="text-align:right;">
            <mu-select v-model="addUserForm.status">
              <mu-option v-for="option, index in status" :key="option" :label="option" :value="option"></mu-option>
            </mu-select>
          </mu-form-item>
        </el-col>
      </el-row>
      <el-row>
        <mu-form-item prop="notes" label="Notes" style="text-align:right;">
          <mu-text-field v-model="addUserForm.notes" multi-line :rows="3" :rows-max="6"></mu-text-field>
        </mu-form-item>
      </el-row>
      <el-row>
        <el-col :span="8" :offset="8">
          <mu-button full-width color="primary" @click="submit"></mu-button>
        </el-col>
      </el-row>
    </mu-form>
  </div>
</template>

<script>
  import * as api from '../../http/api/api';
  import axios from '../../http/index';
  import { guid } from '../../Tools/guid';
  export default {
    data() {
      return {
        labelPosition: 'left',
        textFieldRules: [
          { validate: (val) => !!val, message: 'This is required.' }
        ],
        gender: [
          "UNKNOWN_SEX",
          "MALE",
          "FEMALE"
        ],
        status: [
          'Active',
          'Permanently Inactive'
        ],
        checkboxName: [
          'ReadPlan',
          'ModifyPlan',
          'CreatePlan',
          'DeletePlan',
          'ApprovePlan',
          'AllPlanPrivileges',

          'ModifyPatient',
          'CreatePatient',
          'DeletePatient',
          'ArchivePatient',
        ],
        userType: [
          'Physician',
          'Radiation Oncologist',
          'Dosimetrist',
          'Radiation Therapist',
          'Radiation Physicist',
          'System Administrator',
        ],
        addUserForm: {
          userId: '',
          userName: '',
          sex: '',
          userType: '',
          privilege: [],
          status: '',
          notes: ''
        },
        checkAll: false,
      }
    },
    created() {
      self = this;
    },
    methods: {
      handleCheckAll: function () {
        self.checkAll = self.checkAll;
        if (self.checkAll) {
          self.addUserForm.privilege = self.checkboxName;
        } else {
          self.addUserForm.privilege = [];
        }
        console.log(self.addUserForm.privilege);
      },

      submit: function () {
        let user = {
          id: guid(),
          name: self.addUserForm.userName,
          privileges: self.addUserForm.privilege.length > 0 ? self.addUserForm.privilege[0] : 0
        }
        axios.axiosPost(api.postUserInfo, user);
      }
    }

  }
</script>

<style scoped>
  .mu-demo-form {
    width: 100%;
    max-width: 1000px;
    color: black;
    margin-left: 22%;
    transform: translateY(100px);
  }

  mu-form-item.label {
    font-size: 16px;
  }

  li {
    list-style-type: none;
    margin: 10px;
  }

  .topDiv {
    width: 500px;
  }

  .leftTopDiv {
    float: left;
  }

  .rightTopDiv {
    float: right;
  }
</style>



