import {
  //Module
  SET_PAGENAME,

  //Patient
  UPDATE_PATIENT,

  //Plan
  SET_PLANINFO,

  //User
  SET_USER,
  SET_LOGINDATE,
  SET_USERINFO,

} from './mutation-types'

export default{
  //Module
  [SET_PAGENAME](state, pageName){
    state.pageName = pageName;
    sessionStorage.setItem('pageName', JSON.stringify(pageName))
  },

  //Patient
  [UPDATE_PATIENT](state, patient){
    state.patientInfo = patient;
    sessionStorage.setItem('patientInfo', JSON.stringify(patient));
  },

  //Plan
  [SET_PLANINFO](state, plan){
    state.planInfo = plan;
    sessionStorage.setItem('planInfo', JSON.stringify(plan));
  },

  //User
  [SET_USER] (state,user) {
    state.user = user;
    sessionStorage.setItem('currentUser', JSON.stringify(user));
  },

  [SET_LOGINDATE](state,date){
    state.loginDate = date.getFullYear() + '-' + (date.getMonth() + 1) + '-' + date.getDate();
    sessionStorage.setItem('loginDate', JSON.stringify(state.loginDate))
  },

  [SET_USERINFO](state,userInfo){
    state.userInfo = userInfo;
    sessionStorage.setItem('userInfo', JSON.stringify(state.userInfo))
  }

}


