<template>
  <div>
    <div id="formDiv">
      <mu-form :model="form" class="mu-demo-form" :label-position="labelPosition" label-width="150px">
        <mu-form-item prop="patientMedId" label="Medical Id" style="text-align:right;">
          <mu-text-field v-model="form.patientMedId"></mu-text-field>
        </mu-form-item>
        <mu-form-item prop="name" label="Name" style="text-align:right;">
          <mu-text-field v-model="form.name"></mu-text-field>
        </mu-form-item>
        <mu-form-item prop="sex" label="Gender" style="text-align:right;">
          <mu-select v-model="form.sex">
            <mu-option v-for="option,index in gender" :key="option" :label="option" :value="option"></mu-option>
          </mu-select>
        </mu-form-item>
        <mu-form-item prop="patientType" label="Type" style="text-align:right;">
          <mu-select v-model="form.patientType">
            <mu-option v-for="type,index in types" :key="type" :label="type" :value="type"></mu-option>
          </mu-select>
        </mu-form-item>
        <mu-form-item prop="date" label="Date of Birth" style="text-align:right;">
          <mu-date-input v-model="form.date" container="dialog" :date-time-format="enDateFormat" label-float full-width></mu-date-input>
        </mu-form-item>
        <mu-form-item prop="hisId" label="HIS Id" style="text-align:right;">
          <mu-text-field v-model="form.hisId"></mu-text-field>
        </mu-form-item>
        <mu-form-item prop="notes" label="Notes" style="text-align:right;">
          <mu-text-field multi-line :rows="1" :rows-max="999" v-model="form.notes"></mu-text-field>
        </mu-form-item>
        <mu-form-item style="margin-top:100px">
          <mu-button full-width color="primary" @click="submit"></mu-button>
        </mu-form-item>
      </mu-form>
    </div>
  </div>
</template>

<script>
  import { stringToGBKEncode } from '../../Tools/format';
  import { mapState } from 'vuex';
  import * as api from '../../http/api/api';
  import axios from '../../http/index';
  export default {
    data() {
      return {
        pagename: "Edit Patient",
        gender: [
          "UNKNOWN_SEX",
          "MALE",
          "FEMALE"
        ],
        types: [
          'Patient', 'Phantom'
        ],
        labelPosition: 'left',
        form: {
          patientMedId: '',
          name: '',
          sex: '',
          patientType: '',
          date: '',
          hisId: '',
          notes: '',
        },
        birthDate: undefined,
        enDateFormat,
      }
    },
    created: function () {
      self = this;
    },

    mounted: function () {
      self.form.patientMedId = self.patientInfo.patientMedId;
      self.form.name = self.patientInfo.name;
      self.form.sex = self.patientInfo.sex;
      self.form.patientType = self.patientInfo.type;
      self.form.date = self.patientInfo.birthDate;
      self.form.hisId = "123456";
      self.form.notes = "Hello";
    },

    methods: {
      submit: function () {
        let patientKey = self.patientInfo.patientKey;
        let birthDate = new Date(self.form.date);
        let patient = {
          patientKey: patientKey,
          patientMedId: self.form.patientMedId,
          name: stringToGBKEncode(self.form.name),
          sex: self.form.sex,
          birthDate: {
            year: birthDate.getUTCFullYear(),
            month: birthDate.getUTCMonth() + 1,
            day: birthDate.getUTCDate() + 1
          },
          dcmPatientKeys: self.patientInfo.dcmPatientKeys,
          type: self.form.patientType,
        }
        axios.axiosPut(api.putpatientInfo, { key: patientKey }, patient);
      },
    },
    computed: {
      ...mapState({
        patientInfo: state => state.patientInfo,
      }),
    },
  }
  const dayAbbreviation = ['Sun', 'Mon', 'Tues', 'Wed', 'Thur', 'Fri', 'Sat'];
  //const dayAbbreviation = ['Su', 'M', 'T', 'W', 'T', 'F', 'S'];
  const dayList = ['Sun', 'Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat'];
  const monthList = ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep',
    'Oct', 'Nov', 'Dec'];
  const monthLongList = ['January', 'February', 'March', 'April', 'May', 'June',
    'July', 'August', 'September', 'October', 'November', 'December'];

  const enDateFormat = {
    formatDisplay(date) {
      return `${dayList[date.getDay()]}, ${monthList[date.getMonth()]} ${date.getDate()}`;
    },
    formatMonth(date) {
      return `${monthLongList[date.getMonth()]} ${date.getFullYear()}`;
    },
    getWeekDayArray(firstDayOfWeek) {
      let beforeArray = [];
      let afterArray = [];
      for (let i = 0; i < dayAbbreviation.length; i++) {
        if (i < firstDayOfWeek) {
          afterArray.push(dayAbbreviation[i]);
        } else {
          beforeArray.push(dayAbbreviation[i]);
        }
      }
      return beforeArray.concat(afterArray);
    },
    getMonthList() {
      return monthList;
    }
  };
</script>

<style scoped>
  #formDiv {
    width: 500px;
    margin-left: 720px;
    transform: translateY(100px); /****/
  }

  .mu-demo-form {
    width: 100%;
    max-width: 500px;
    color: black;
  }

  mu-form-item.label {
    font-size: 16px;
  }
</style>



