<template>
  <div>
    <div class="qaPlanDiv">
      <v-table :height="300"
      :columns="qaPlanColumns"
      :table-data="qaPlanData"
      row-hover-color="#ee9"
      row-click-color="#F0E0FF"
      title-bg-color="#BBB8BF"
      odd-bg-color="#e0f7fa"
      even-bg-color="#b2dfdb"></v-table>
    </div>
    <div class="tabContent">
      <mu-tabs class="mutabs" inverse :value.sync="pageIndex" text-color="#1b225b">
        <mu-tab>QA Plan Fractions</mu-tab>
        <mu-tab>QA Plan Details</mu-tab>
        <mu-tab>QA Plan Dicom Series</mu-tab>
      </mu-tabs>
      <div v-if="pageIndex === 0">
        <QAPlanFractions></QAPlanFractions>
      </div>

      <div v-if="pageIndex === 1">
        <QAPlanDetails></QAPlanDetails>
      </div>
      <div v-if="pageIndex === 2">
        <QAPlanDicomSeries></QAPlanDicomSeries>
      </div>
    </div>
  </div>
</template>

<script>
  import QAPlanFractions from '../../components/QAPlanFractions.vue';
  import QAPlanDetails from '../../components/QAPlanDetails.vue';
  import QAPlanDicomSeries from '../../components/QAPlanDicomSeries.vue';
  export default {
    components: {
      QAPlanFractions,
      QAPlanDetails,
      QAPlanDicomSeries,
    },
    data() {
      return {
        qaPlanData:[],
        qaPlanColumns: [
          {
            title: "Phantom Name",
            field: "phantomName",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "QA Plan Name",
            field: "qaPlanName",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Status",
            field: "status",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Fraction MU",
            field: "fractionMU",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Delivery Time Estimate",
            field: "deliveryTimeEstimate",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Last Treatment Date(YYYY-mm-dd)",
            field: "lastTreatmentDate",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Last Fraction Result",
            field: "lastFractionResult",
            width: 273,
            titleAlign: "center",
            columnAlign: "center",
          },
        ],
        pageIndex:0,
      }
    }
  }
</script>

<style scoped>
  .qaPlanDiv {
    width:100%;
    height:300px;
  }

  .mutabs /deep/ .mu-tab .mu-tab-wrapper{
    height: 30px;
    min-height: 30px;
  }

  .mutabs /deep/ .mu-tab{
    transform: translateX(15px);
  }

  .mutabs /deep/ .mu-tab-active{
    transform:translateX(15px);
    background: border-box rgb(207, 196, 196) ;
    font-weight: bold;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }

  .mutabs /deep/ mu-tabs {
    background-color: rgb(180, 180, 180);
  }
</style>



