export function gbkToStringEncode(gbkStr){
  // 
  let str = require("iconv-lite").decode(
    new Buffer(gbkStr, "base64"),
    "gbk"
  );
  return str;
}

export function encodeFormatter(row, column, cellValue) {
  let x = new Uint8Array(cellValue);
  let str = new TextDecoder("gbk").decode(x);
  return str;
}

//GBK
export function stringToGBKEncode(str) {
  // 
  var gbkStr = new Buffer(
    require("iconv-lite").encode(str, "gbk")
  ).toString("base64");
  return gbkStr;
}

export function dateFormat(fmt, date) {
  let ret;
  const opt = {
      "Y+": date.getFullYear().toString(),        // 
      "m+": (date.getMonth() + 1).toString(),     // 
      "d+": date.getDate().toString(),            // 
      "H+": date.getHours().toString(),           // 
      "M+": date.getMinutes().toString(),         // 
      "S+": date.getSeconds().toString()          // 
      // 
  };
  for (let k in opt) {
      ret = new RegExp("(" + k + ")").exec(fmt);
      if (ret) {
          fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
      };
  };
  return fmt;
}

export function dateFormatOfDbPatient(value) {
  let birthDate = value.year + "-" + value.month + "-" + value.day;
  let date = new Date(birthDate);
  return dateFormat('YYYY-mm-dd', date);
}



