<template>
  <div>
    <div class="searchDiv">
      <input type="text"
             class="search"
             placeholder="medicalId/patientName"
             v-model="condition"
             @input="txtChange" />
      <el-tooltip class="tooltip" content="Import patient" placement="bottom" effect="light">
        <svg-icon class-name="import" icon-class="jiajianzujianjiahao" @mouseup="goImportPatientPage" />
      </el-tooltip>
      <el-tooltip class="tooltip" content="Edit patient" placement="bottom" effect="light">
        <svg-icon class-name="edit" icon-class="shouhuodizhiyebianji" @mouseup="editPatient"></svg-icon>
      </el-tooltip>
      <el-tooltip class="tooltip" content="Delete patient" placement="bottom" effect="light">
        <svg-icon class-name="delete" icon-class="guanbi" @mouseup="deletePatients"></svg-icon>
      </el-tooltip>
    </div>
    <div class="patientListDiv">
      <mu-data-table border
                     selectable
                     select-all
                     checkbox
                     :columns="columns"
                     height="970px"
                     :selects.sync="selects"
                     :sort.sync="sort"
                     @sort-change="handleSortChange"
                     :data="searchData"
                     :rowStyle="setStyle">
        <template slot="expand" slot-scope="prop">
          <div id="patientDetails" style="background:gray; height:300px;">
            <div id="patientPhoto"
                 style="width:100px;height:120px;background:red;margin-left:10px;margin-top:10px;float:left;"></div>
            <div id="patientcontain"
                 style="width:800px;height:280px;background:green;float:left;margin-top:10px;margin-left:10px;color:white;font-size:16px;padding:10px;">
              <el-row>
                <el-col :span="3">Medical Id:</el-col>
                <el-col :span="11">
                  <input type="text" v-model="prop.row.patientMedId" style="font-size:16px;height:25px;width:340px" disabled />
                </el-col>
                <el-col :span="3">Type:</el-col>
                <el-col :span="7">
                  <div class="grid-content bg-purple-light">
                    <input type="text" v-model="prop.row.type" style="font-size:16px;height:25px" disabled />
                  </div>
                </el-col>
              </el-row>
              </br>
              <el-row>
                <el-col :span="3">Name:</el-col>
                <el-col :span="11">
                  <div class="grid-content bg-purple-light">
                    <input type="text" v-model="prop.row.name" style="font-size:16px;height:25px;width:340px" disabled />
                  </div>
                </el-col>
                <el-col :span="3">Date of Birth:</el-col>
                <el-col :span="7">
                  <div class="grid-content bg-purple-light">
                    <input type="text" v-model="prop.row.birthDate" style="font-size:16px;height:25px" disabled />
                  </div>
                </el-col>
              </el-row>
              </br>
              <el-row>
                <el-col :span="3">HIS Id:</el-col>
                <el-col :span="11">
                  <div class="grid-content bg-purple-light">
                    <input type="text" v-model="prop.row.type" style="font-size:16px;height:25px;width:340px" disabled />
                  </div>
                </el-col>
                <el-col :span="3">Gender:</el-col>
                <el-col :span="7">
                  <div class="grid-content bg-purple-light">
                    <input type="text" v-model="prop.row.sex" style="font-size:16px;height:25px" disabled />
                  </div>
                </el-col>
              </el-row>
              </br>
              <el-row>
                <el-col :span="3">Notes:</el-col>
                <el-col :span="21">
                  <textarea style="width:640px;height:100px" disabled readonly>{{prop.row.notes}}</textarea>
                </el-col>
              </el-row>
            </div>
          </div>
        </template>
        <template slot-scope="scope">
          <td class="is-center">{{scope.row.index}}</td>
          <td class="is-center">{{scope.row.patientMedId}}</td>
          <td class="is-center">{{scope.row.name}}</td>
          <td class="is-center">{{scope.row.sex}}</td>
          <td class="is-center">{{scope.row.birthDate}}</td>
          <td class="is-center">{{scope.row.type}}</td>
        </template>
      </mu-data-table>
    </div>
  </div>
</template>

<script>
  import * as api from '../../http/api/api';
  import axios from '../../http/index';
  import { dateFormatOfDbPatient, gbkToStringEncode } from '../../Tools/format';
  import { mapActions } from 'vuex';

  export default {
    data() {
      return {
        selects: [],
        sort: {
          name: "",
          order: "asc"
        },
        columns: [
          {
            title: "Index",
            name: "index",
            width: 200,
            align: "center",
            sortable: true
          },
          {
            title: "MedicalId",
            name: "patientMedId",
            width: 400,
            align: "center",
            sortable: true
          },
          {
            title: "Name",
            name: "name",
            width: 300,
            align: "center",
          },
          {
            title: "Gender",
            name: "sex",
            width: 300,
            align: "center",
          },
          {
            title: "Date of Birth (YYYY-mm-dd)",
            name: "birthDate",
            width: 300,
            align: "center",
          },
          {
            title: "Type",
            name: "type",
            width: "300",
            align: "center",
          }
        ],
        tableData: [],
        searchData: [],
        value: "",
        condition: ""
      };
    },
    created() {
      self = this;
    },
    mounted: function () {
      self.refresh();
    },
    methods: {
      ...mapActions({
        savePatientInfo: 'savePatient',
      }),
      handleSortChange({ name, order }) {
        self.searchData = self.searchData.sort((a, b) =>
          order === "asc" ? a[name] - b[name] : b[name] - a[name]
        );
      },

      txtChange: function () {
        self.searchData = [];
        for (let data of self.tableData) {
          if (
            data.name.toLowerCase().indexOf(self.condition.toLowerCase()) != -1 ||
            data.medicalId.toLowerCase().indexOf(self.condition.toLowerCase()) !=
            -1
          ) {
            self.searchData.push(data);
          }
        }

        if (self.condition === "") {
          self.searchData = self.tableData;
        }
      },

      goImportPatientPage: function () {
        self.$router.push({
          name: "Importpatient"
        });
      },

      refresh: async function () {
        await self.getDbAllPatients();
        self.searchData = self.tableData;
      },

      editPatient: function () {
        if (self.selects.length == 0) {
          self.$alert("Please select a patient");
        } else if (self.selects.length > 1) {
          self.$alert("Only one patient can be selected");
        } else {
          self.savePatientInfo(self.searchData[self.selects[0]]);
          self.$router.push({
            name: "EditPatient"
          });
        }
      },

      //
      deletePatients: async function () {
        if (self.selects.length == 0) {
          self.$alert("Please select patients");
        }
        let keys = [];
        for (let idx of self.selects) {
          keys.push(self.searchData[idx].patientKey);
        }

        for (let key of keys) {
          await axios.axiosDelete(api.deletePatientInfo, { key: key });
        }
        self.selects = [];
        await self.refresh();
        self.txtChange();
      },

      //
      getDbAllPatients: async function () {
        let patients = await axios.axiosGet(api.getAllPatientInfos).catch(err => {
          if (err.response.status === 404) {
            self.tableData = [];
            self.searchData = [];
            self.selects = [];
          }
        });
        if (patients !== undefined) {
          let idx = 0;
          for (let patient of patients) {
            patient.index = ++idx;
            patient.birthDate = dateFormatOfDbPatient(patient.birthDate);
            patient.name = gbkToStringEncode(patient.name);
          }
          self.tableData = patients;
          console.log("");
        }
      },

      setStyle(row, rowIndex) {
        return "color:green;font-size:18px;";
      }
    },
  };
</script>

<style scoped>
  * {
    padding: 0px;
    margin: 0px;
    position: relative;
  }

  #patientAdmin {
    background-color: #1b225b;
    width: 1920px;
    height: 1086px;
  }

  #containDiv {
    background-color: #cddde8;
    width: 1900px;
    height: 1020px;
    margin-left: 10px;
    margin-top: 10px;
  }

  .searchDiv,
  .patientListDiv {
    width: 100%;
  }

  .searchDiv {
    height: 50px;
  }

  .patientListDiv {
    height: 970px;
  }

    .patientListDiv /deep/ .mu-table-header-wrapper .mu-table-header th {
      height: 30px;
    }

  .search {
    width: 250px;
    float: left;
    margin-top: 10px;
    margin-left: 10px;
  }

  input {
    border: 1px solid #ccc;
    padding: 7px 0px;
    border-radius: 3px;
    padding-left: 5px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out 0.15s, -webkit-box-shadow ease-in-out 0.15s;
    -moz-transition: border-color ease-in-out 0.15s, -moz-box-shadow ease-in-out 0.15s;
    -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  }

    input:focus {
      border-color: #66afe9;
      outline: 0;
      -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(37, 72, 100, 0.6);
      -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
      box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
    }

  .import,
  .edit,
  .delete {
    color: #1bd930;
    float: left;
    margin-left: 20px;
    margin-top: 13px;
    font-size: 25px;
  }

  .import:hover {
    color: #7eb781;
  }

  .import:active {
    color: #4f9153;
  }

  .edit {
    color: #423f3f;
    font-size: 30px;
    margin-top: 10px;
  }

  .edit:hover {
    color: #a29b9b;
  }

  .edit:active {
    color: #775e5e;
  }

  .delete {
    color: #f41b10;
  }

  .delete:hover {
    color: #d22727;
  }

  .delete:active {
    color: #a22626;
  }

  .tooltip {
    float: left;
  }
</style>



