<template>
  <div>
    <div class="searchDiv">
      <input type="text"
             class="search"
             placeholder="medicalId/patientName"
             v-model="condition"
             @input="txtChange" />
      <el-tooltip class="importpatientTooltip" content="Import" placement="bottom" effect="light">
        <svg-icon class="import" class-name="import" icon-class="import" @mouseup="dataNavigation" />
      </el-tooltip>
    </div>
    <div class="patientListDiv">
      <mu-data-table border
                     selectable
                     select-all
                     checkbox
                     :columns="columns"
                     height="970px"
                     :selects.sync="selects"
                     :sort.sync="sort"
                     @sort-change="handleSortChange"
                     :data="searchDate"
                     :rowStyle="setStyle">
        <template slot="expand" slot-scope="prop">
          <div id="patientDetails" style="background:gray; height:300px;">
            <div id="patientPhoto"
                 style="width:100px;height:120px;background:red;margin-left:10px;margin-top:10px;float:left;"></div>
            <div id="patientcontain"
                 style="width:800px;height:280px;background:green;float:left;margin-top:10px;margin-left:10px;color:white;font-size:16px;padding:10px;">
              <el-row>
                <el-col :span="3">Medical Id:</el-col>
                <el-col :span="11">
                  <input type="text"
                         v-model="prop.row.MainDicomTags.PatientID"
                         style="font-size:16px;height:25px;width:340px"
                         disabled />
                </el-col>
                <el-col :span="3">Type:</el-col>
                <el-col :span="7">
                  <div class="grid-content bg-purple-light">
                    <input type="text"
                           v-model="prop.row.Type"
                           style="font-size:16px;height:25px"
                           disabled />
                  </div>
                </el-col>
              </el-row>
              </br>
              <el-row>
                <el-col :span="3">Name:</el-col>
                <el-col :span="11">
                  <input type="text"
                         v-model="prop.row.MainDicomTags.PatientName"
                         style="font-size:16px;height:25px;width:340px"
                         disabled />
                </el-col>
                <el-col :span="3">Date of Birth:</el-col>
                <el-col :span="7">
                  <input type="text"
                         v-model="prop.row.MainDicomTags.PatientBirthDate"
                         style="font-size:16px;height:25px"
                         disabled />
                </el-col>
              </el-row>
              </br>
              <el-row>
                <el-col :span="3">HIS Id:</el-col>
                <el-col :span="11">
                  <input type="text"
                         v-model="prop.row.Type"
                         style="font-size:16px;height:25px;width:340px"
                         disabled />
                </el-col>
                <el-col :span="3">Gender:</el-col>
                <el-col :span="7">
                  <input type="text"
                         v-model="prop.row.MainDicomTags.PatientSex"
                         style="font-size:16px;height:25px"
                         disabled />
                </el-col>
              </el-row>
              </br>
              <el-row>
                <el-col :span="3">Notes:</el-col>
                <el-col :span="21">
                  <textarea style="width:640px;height:100px" disabled readonly>{{prop.row.notes}}</textarea>
                </el-col>
              </el-row>
            </div>
          </div>
        </template>
        <template slot-scope="scope">
          <td class="is-center">{{scope.row.index}}</td>
          <td class="is-center">{{scope.row.MainDicomTags.PatientID}}</td>
          <td class="is-center">{{scope.row.MainDicomTags.PatientName}}</td>
          <td class="is-center">{{scope.row.MainDicomTags.PatientSex}}</td>
          <td class="is-center">{{scope.row.MainDicomTags.PatientBirthDate}}</td>
          <td class="is-center">{{scope.row.Type}}</td>
        </template>
      </mu-data-table>
    </div>
  </div>
</template>

<script>
  import {
    gbkToStringEncode,
    stringToGBKEncode,
  } from "../../Tools/format";
  import { guid } from "../../Tools/guid";
  import { PatientSex, DcmPatientSex } from "../../constants/enum";
  import * as api from '../../http/api/api';
  import axios from '../../http/index';

  export default {
    data() {
      return {
        current: 1,
        selects: [],
        sort: {
          name: "",
          order: "asc"
        },
        columns: [
          {
            title: "Index",
            name: "index",
            width: 200,
            align: "center",
            sortable: true
          },
          { title: "MedicalId", name: "PatientID", width: 400, align: "center" },
          { title: "Name", name: "PatientName", width: 300, align: "center" },
          { title: "Gender", name: "PatientSex", width: 300, align: "center" },
          {
            title: "Date of Birth (YYYY-mm-dd)",
            name: "PatientBirthDate",
            width: 300,
            align: "center"
          },
          { title: "Type", name: "Type", width: "300", align: "center" }
        ],
        tableData: [],
        searchDate: [],
        condition: ""
      };
    },
    created() {
      self = this;
    },
    mounted: function () {
      self.loadDcmPatient();
    },
    methods: {
      loadDcmPatient: async function () {
        await self.getDbNotExistPatients();
      },

      handleSortChange({ name, order }) {
        self.searchDate = self.searchDate.sort((a, b) =>
          order === "asc" ? a[name] - b[name] : b[name] - a[name]
        );
      },

      txtChange: function () {
        self.searchDate = [];
        for (let data of self.tableData) {
          if (
            data.MainDicomTags.PatientName.toLowerCase().indexOf(
              self.condition.toLowerCase()
            ) != -1 ||
            data.MainDicomTags.PatientID.toLowerCase().indexOf(
              self.condition.toLowerCase()
            ) != -1
          ) {
            self.searchDate.push(data);
          }
        }

        if (self.condition === "") {
          self.searchDate = self.tableData;
        }
      },

      //Orthanc serverDMS
      dataNavigation: async function () {
        if (self.selects.length === 0) {
          self.$alert("Please select patients");
        }
        let patients = [];
        for (let idx of self.selects) {
          let dcmPatient = self.searchDate[idx];

          let birthDate;
          let patientBirthDate = dcmPatient.MainDicomTags.PatientBirthDate;
          if (
            typeof patientBirthDate == "undefined" ||
            patientBirthDate == null ||
            patientBirthDate == ""
          ) {
            birthDate = new Date();
          } else {
            if (isNaN(patientBirthDate) && !isNaN(Date.parse(patientBirthDate))) {
              birthDate = new Date(Date.parse(patientBirthDate));
            } else {
              birthDate = new Date(patientBirthDate * 1000);
            }
          }

          let patient = {
            patientKey: guid(),
            patientMedId: dcmPatient.MainDicomTags.PatientID,
            name: stringToGBKEncode(dcmPatient.MainDicomTags.PatientName),
            sex: dcmPatient.MainDicomTags.PatientSex,
            birthDate: {
              year: birthDate.getUTCFullYear(),
              month: birthDate.getUTCMonth() + 1,
              day: birthDate.getUTCDate()
            },
            dcmPatientKeys: [dcmPatient.ID]
          };
          patients.push(patient);
        }

        for (let patient of patients) {
          await self.postPatientData(patient);
        }
        self.selects = [];
        await self.loadDcmPatient();
        self.txtChange();
      },

      //DMSOrthanc Server
      getDbNotExistPatients: async function () {
        let patientsKey = [];
        let dbPatientsKey = await self.getDbAllDcmPatientsId();
        let dcmPatientKeys = await axios.axiosGet(api.getAllDcmPatientKeys);
        if (dcmPatientKeys !== null) {
          for (let dcmPatientKey of dcmPatientKeys) {
            if (!dbPatientsKey.includes(dcmPatientKey)) {
              patientsKey.push(dcmPatientKey);
            }
          }
        } else {
          patientsKey.push(dcmPatientKeys);
        }

        let patients = [];
        let idx = 1;
        for (let key of patientsKey) {
          let patient = await self.getDcmPatient(key);
          patient.index = idx;
          patients.push(patient);
          idx++;
        }
        self.searchDate = patients;
        self.tableData = patients;
        return patients;
      },

      //DMSDcmPatientId
      getDbAllDcmPatientsId: async function () {
        let dcmPatientsKey = [];
        let patientsInfo = await axios.axiosGet(api.getAllPatientInfos).catch(err => { //getAllPatients().catch(err => {
          if (err.response.status === 404) {
            return dcmPatientsKey;
          }
        });
        for (let patient of patientsInfo) {
          for (let dcmPatientKey of patient.dcmPatientKeys) {
            dcmPatientsKey.push(dcmPatientKey);
          }
        }
        return dcmPatientsKey;
      },

      postPatientData: async function (patient) {
        try {
          let successFlag = await axios.axiosPost(api.postPatientInfo, patient);
          let name = gbkToStringEncode(successFlag.name);
          console.log(patient.patientMedId + " Success " + name);
        } catch (err) {
          console.error(err.toString());
          self.$alert(err, "Error");
        }
      },

      getDcmPatient: async function (key) {
        let dcmPatient = await axios.axiosGet(api.getDcmPatientInfo, { key: key });
        let sex = "";
        if (dcmPatient.MainDicomTags.PatientSex === DcmPatientSex.M) {
          sex = PatientSex.MALE;
        } else if (dcmPatient.MainDicomTags.PatientSex === DcmPatientSex.F) {
          sex = PatientSex.FEMALE;
        } else {
          sex = PatientSex.UNKNOWN_SEX;
        }
        dcmPatient.MainDicomTags.PatientSex = sex;
        return dcmPatient;
      },

      //js
      setStyle(row, rowIndex) {
        return "color:green;font-size:18px;";
      }
    }
  };

  Array.prototype.remove = function (val) {
    var index = this.indexOf(val);
    if (index > -1) {
      this.splice(index, 1);
    }
  };
</script>

<style scoped>
  #importPatientDiv {
    background-color: #1b225b;
    width: 1920px;
    height: 1086px;
  }

  #containDiv {
    background-color: #cddde8;
    width: 1900px;
    height: 1020px;
    margin-left: 10px;
    margin-top: 10px;
  }

  .searchDiv,
  .patientListDiv {
    width: 100%;
  }

  .searchDiv {
    height: 50px;
  }

  .patientListDiv {
    height: 970px;
  }

  .search {
    width: 250px;
    float: left;
    margin-top: 10px;
    margin-left: 10px;
  }

  input {
    border: 1px solid #ccc;
    padding: 7px 0px;
    border-radius: 3px;
    padding-left: 5px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out 0.15s, -webkit-box-shadow ease-in-out 0.15s;
    -moz-transition: border-color ease-in-out 0.15s, -moz-box-shadow ease-in-out 0.15s;
    -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  }

  input:focus {
    border-color: #66afe9;
    outline: 0;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
    -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075), 0 0 8px rgba(102, 175, 233, 0.6);
  }

  .import {
    color: #1bd930;
    float: left;
    margin-left: 20px;
    margin-top: 13px;
    font-size: 25px;
  }

  .import:hover {
    color: #7eb781;
  }

  .import:active {
    color: #4f9153;
  }
</style>



