<template>
  <div class="bannerDiv">
    <div class="logoDiv"></div>
    <div class="pagename">{{ pageName }}</div>
    <div class="date">{{ loginDate }}</div>
    <div class="username">{{ currentUser.name }}</div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: "banner",
  props: {
    pagename: String,
    username: String,
    logindate: String
  },
  computed: {
    ...mapState({
      pageName: state => state.pageName,
      currentUser: state => state.user,
      loginDate: state => state.loginDate,
    })
  },
};
</script>

<style scope>
.bannerDiv {
  position: relative;
  width: 1920px;
  height: 50px;
  background: linear-gradient(left, #90a3df, #000000);
}

.logoDiv {
  width: 210px;
  height: 40px;
  background: url(../assets/rayerLogo.png) no-repeat;
  background-size: cover;
  margin-left: 20px;
  float: left;
  margin-top: 8px;
}

.pagename {
  width: 400px;
  height: 50px;
  color: #b9b9b9;
  font-size: 30px;
  float: left;
  margin-left: 530px;
  line-height: 50px;
}

.username,
.date {
  width: 150px;
  height: 50px;
  line-height: 50px;
  font-size: 25px;
  float: right;
  color: #e1cfcf;
}

.username {
  margin-right: 0px;
}

.date {
  margin-right: 100px;
}
</style>



