<template>
  <div>
    <mu-tabs class="tabPages" text-color="#1b225b">
      <router-link style="color: white;" :to="{name: 'PlanDetails'}">
        <mu-tab>
          Plan Details
        </mu-tab>
      </router-link>
      <router-link style="color: white;" :to="{name: 'FractionMng'}">
        <mu-tab>
          Fraction Management
        </mu-tab>
      </router-link>
      <router-link style="color: white;" :to="{name: 'QAPlans'}">
        <mu-tab>
          QA Plans
        </mu-tab>
      </router-link>
    </mu-tabs>
    <div>
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        pageIndex: 0,
      }
    },
  }
</script>

<style lang="less" scoped>
  .tabPages /deep/ .mu-tab .mu-tab-wrapper {
    height: 30px;
    min-height: 30px;
  }

  .tabPages /deep/ .mu-tab {
    transform: translateX(15px);
  }

  .tabPages /deep/ .mu-tab-active {
    transform: translateX(15px);
    background: #1166ff;
    font-weight: bold;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
  }

</style>



