<template>
  <div class="containDiv">
    <div class="useradmin userList">
      <div class="searchDiv">
        <input type="text"
               class="search"
               placeholder="userId/userName"
               v-model="condition"
               @input="txtChange" />
        <el-tooltip class="tooltip" content="Add User" placement="bottom" effect="light">
          <svg-icon class-name="import" icon-class="jiajianzujianjiahao" @mouseup="goAddUserPage" />
        </el-tooltip>
        <el-tooltip class="tooltip" content="Edit User" placement="bottom" effect="light">
          <svg-icon class-name="edit" icon-class="shouhuodizhiyebianji" @mouseup="goEditUserPage"></svg-icon>
        </el-tooltip>
        <el-tooltip class="tooltip" content="Delete User" placement="bottom" effect="light">
          <svg-icon class-name="delete" icon-class="guanbi" @mouseup="deleteSelectedUser"></svg-icon>
        </el-tooltip>
      </div>
      <v-table style="width:100%;"
               :height="500"
               :columns="userColumns"
               :table-data="searchData"
               row-hover-color="#ee9"
               row-click-color="#F0E0FF"
               title-bg-color="#BBB8BF"
               odd-bg-color="#e0f7fa"
               even-bg-color="#b2dfdb"
               :select-change="selectUserChange"
               :row-click="selectUser"
               :select-all="selectAll"></v-table>
    </div>
    <div class="useradmin userDetails">
      <mu-form :model="userDetailsForm" class="specialistsForm" label-position="top" label-width="250px" style="margin:10px">
        <el-row>
          <el-col :span="12">
            <el-row>
              <el-col :span="12">
                <mu-form-item prop="userName" label="User Name" style="text-align:left;">
                  <input v-model="userDetailsForm.userName" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="12">
                <mu-form-item prop="lastLoginDateAndTime" label="Last Login Date And Time" style="text-align:left;">
                  <input v-model="userDetailsForm.lastLoginDateAndTime" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <mu-form-item prop="userId" label="User Id" style="text-align:left;">
                  <input v-model="userDetailsForm.userId" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="12">
                <mu-form-item prop="failedLoginCount" label="Failed Login Count" style="text-align:left;">
                  <input v-model="userDetailsForm.state" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <mu-form-item prop="state" label="Status" style="text-align:left;">
                  <input v-model="userDetailsForm.state" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                <mu-form-item prop="createDateTime" label="Create DateTime" style="text-align:left;">
                  <input v-model="userDetailsForm.createDateTime" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
          </el-col>
          <el-col :span="12">
            <mu-form-item prop="notes" label="Notes" style="text-align:left;">
              <textarea v-model="userDetailsForm.notes" rows="14" cols="150" style="resize:none" disabled />
            </mu-form-item>
          </el-col>
        </el-row>
      </mu-form>
    </div>
  </div>
</template>

<script>
  import * as api from '../../http/api/api';
  import axios from '../../http/index';
  import { mapMutations } from 'vuex';
  export default {
    data() {
      return {
        userColumns: [
          {
            width: 60,
            titleAlign: 'center',
            columnAlign: 'center',
            type: 'selection'
          },
          {
            title: "Index",
            field: "index",
            width: 150,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "User Name",
            field: "name",
            width: 417.5,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "User ID",
            field: "id",
            width: 417.5,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Status",
            field: "state",
            width: 417.5,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "User Type",
            field: "userType",
            width: 417.5,
            titleAlign: "center",
            columnAlign: "center",
          }
        ],
        tableData: [],
        searchData: [],
        selects: [],
        condition: "",
        userDetailsForm: {
          userName: '',
          lastLoginDateAndTime: '',
          userId: '',
          failedLoginCount: '',
          state: '',
          createDateTime: '',
          notes: '',
        },
        editUser: {},
      }
    },
    created() {
      self = this;
    },
    mounted() {
      self.loadUser();
    },
    methods: {
      loadUser: async function () {
        let usersId = await axios.axiosGet(api.getAllUserKeys).catch(err => {
          if (err.response.status === 404) {
            self.tableData = [];
            self.searchData = [];
            self.selects = [];
          }
        });
        if (usersId !== undefined) {
          let usersList = [];
          let idx = 1;
          for (let id of usersId) {
            let user = await axios.axiosGet(api.getUserInfo, { key: id });
            user.index = idx++;
            usersList.push(user);
          }
          self.searchData = usersList;
          self.tableData = usersList;
        }
      },

      txtChange: function () {
        self.searchData = [];
        for (let data of self.tableData) {
          if (
            data.name.toLowerCase().indexOf(self.condition.toLowerCase()) != -1 ||
            data.id.toLowerCase().indexOf(self.condition.toLowerCase()) !=
            -1
          ) {
            self.searchData.push(data);
          }
        }

        if (self.condition === "") {
          self.searchData = self.tableData;
        }
      },

      selectUserChange: async function (selection, rowData) {
        self.selects = selection;
      },

      selectUser: function (rowIndex, rowData, column) {
        let user = rowData;
        self.userDetailsForm.userName = user.name;
        self.userDetailsForm.userId = user.id;

        self.editUser = rowData;
      },

      selectAll: function (selection) {
        self.selects = selection;
      },

      goAddUserPage: function () {
        self.$router.push({
          name: "AddUser"
        });
      },

      goEditUserPage: function () {
        if (self.editUser !== null && self.editUser !== undefined && self.editUser.id !== '' && self.editUser.id !== undefined) {
          self.SET_USERINFO(self.editUser);
        } else {
          self.$alert("Please select a user");
          return;
        }

        self.$router.push({
          name: "EditUser"
        });
      },

      deleteSelectedUser: async function () {
        if (self.selects.length > 0) {
          for (let user of self.selects) {
            await axios.axiosDelete(api.deleteUserInfo, { key: user.id });
          }
          await self.loadUser();
        } else {
          self.$alert("Please select users");
        }
      },
      ...mapMutations([
        'SET_USERINFO',
      ])
    },
  }
</script>

<style scoped>

  #containDiv {
    background-color: #cddde8;
    width: 1900px;
    height: 1020px;
    margin-left: 10px;
    margin-top: 10px;
  }

  .useradmin {
    background-color: #7A9EA6;
    width: 1880px;
    margin-left: 10px;
  }

  .userList {
    height: 600px;
    transform: translateY(10px);
  }

  .userDetails {
    height: 390px;
    transform: translateY(10px);
  }

  .search {
    width: 250px;
    float: left;
    margin-top: 5px;
    margin-left: 10px;
  }

  input, textarea {
    width: 80%;
    border: 1px solid #ccc;
    padding: 5px 0px;
    border-radius: 3px;
    padding-left: 5px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out 0.15s, -webkit-box-shadow ease-in-out 0.15s;
    -moz-transition: border-color ease-in-out 0.15s, -moz-box-shadow ease-in-out 0.15s;
    -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  }

  .import,
  .edit,
  .delete {
    color: #1bd930;
    float: left;
    margin-left: 20px;
    margin-top: 8px;
    font-size: 25px;
  }

  .import:hover {
    color: #7eb781;
  }

  .import:active {
    color: #4f9153;
  }

  .edit {
    color: #423f3f;
    font-size: 30px;
    margin-top: 5px;
  }

  .edit:hover {
    color: #a29b9b;
  }

  .edit:active {
    color: #775e5e;
  }

  .delete {
    color: #f41b10;
  }

  .delete:hover {
    color: #d22727;
  }

  .delete:active {
    color: #a22626;
  }

  .tooltip {
    float: left;
  }
</style>



