import Vue from 'vue';
import router from './router';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import App from './App.vue';
import VueResource from 'vue-resource';
import Axios from './http/index'
import './icons'
import { DataTable, Pagination,Form, Slider,Switch,Radio,Checkbox, DateInput, TextField, Select, Icon,Button,Tabs} from 'muse-ui';
import 'muse-ui/dist/muse-ui.css';
import store from './store';
import 'vue-easytable/libs/themes-base/index.css'
import {VTable, VPagination} from 'vue-easytable'

Vue.use(ElementUI);
Vue.use(Axios);
Vue.use(VueResource);
Vue.use(DataTable);
Vue.use(Pagination);
Vue.use(Form);
Vue.use(Slider);
Vue.use(Switch);
Vue.use(Radio);
Vue.use(Checkbox);
Vue.use(DateInput);
Vue.use(TextField);
Vue.use(Select);
Vue.use(Icon);
Vue.use(Button);
Vue.use(Tabs);
Vue.use(VTable);
Vue.use(VPagination);
Vue.prototype.$axios = Axios;

new Vue({
  router,
  Axios,
  store,
  render: h => h(App)
}).$mount('#app')


