<template>
  <div>
    <el-row>
      <div class="grid-content patientList">
        <h3 style="text-align:left;margin-left:10px;">
          Patients
          <input is-horizontal-resize=true
                 is-vertical-resize=true
                 type="text"
                 class="search"
                 placeholder="medicalId/patientName"
                 v-model="condition"
                 @input="txtChange" />
        </h3>
        <v-table :height="320"
                 :columns="patientsColumns"
                 :table-data="searchData"
                 row-hover-color="#ee9"
                 row-click-color="#F0E0FF"
                 title-bg-color="#BBB8BF"
                 odd-bg-color="#e0f7fa"
                 even-bg-color="#b2dfdb"
                 :select-change="selectPatientChange"></v-table>
      </div>
    </el-row>
    <el-row>
      <div class="grid-content treatmentPlans">
        <h3 style="text-align:left;margin-left:10px;">Treatment Plans</h3>
        <v-table style="width:100%"
                 :columns="treatmentPlanColumns"
                 :table-data="treatmentPlansData"
                 row-hover-color="#ee9"
                 row-click-color="#F0E0FF"
                 title-bg-color="#BBB8BF"
                 odd-bg-color="#e0f7fa"
                 even-bg-color="#b2dfdb"
                 :select-change="selectChange"
                 :row-dblclick="rowDblClick"></v-table>
      </div>
    </el-row>
    <el-row>
      <div class="grid-content qaPlans">
        <h3 style="text-align:left;margin-left:10px;">QA Plans</h3>
        <v-table style="width:100%"
                 :columns="qaPlanColumns"
                 :table-data="qaPlansData"
                 row-hover-color="#ee9"
                 row-click-color="#F0E0FF"
                 title-bg-color="#BBB8BF"
                 odd-bg-color="#e0f7fa"
                 even-bg-color="#b2dfdb"></v-table>
      </div>
    </el-row>
  </div>
</template>

<script>
  import * as api from '../../http/api/api';
  import axios from '../../http/index';
  import { dateFormatOfDbPatient, gbkToStringEncode } from '../../Tools/format'
  export default {
    data() {
      return {
        selectedPatient: [],
        pagename: 'Plan Admin',
        condition: '',
        patientsColumns: [
          {
            width: 60,
            titleAlign: 'center',
            columnAlign: 'center',
            type: 'selection'
          },
          {
            title: "Index",
            field: "index",
            width: 150,
            titleAlign: "center",
            columnAlign: "center",
            isResize: true
          },
          {
            title: "MedicalId",
            field: "patientMedId",
            width: 338,
            titleAlign: "center",
            columnAlign: "center",
            isResize: true
          },
          {
            title: "Name",
            field: "name",
            width: 338,
            titleAlign: "center",
            columnAlign: "center",
            isResize: true
          },
          {
            title: "Gender",
            field: "sex",
            width: 338,
            titleAlign: "center",
            columnAlign: "center",
            isResize: true
          },
          {
            title: "Date of Birth (YYYY-mm-dd)",
            field: "birthDate",
            width: 338,
            titleAlign: "center",
            columnAlign: "center",
            isResize: true
          },
          {
            title: "Type",
            field: "type",
            width: 338,
            titleAlign: "center",
            columnAlign: "center",
            isResize: true
          }
        ],
        patientTableData: [],
        searchData: [],
        //Plan
        searchPlanData: [],
        selectedPlan: [],
        treatmentPlansData: [],
        treatmentPlanColumns: [
          {
            width: 60,
            titleAlign: 'center',
            columnAlign: 'center',
            type: 'selection'
          },
          {
            title: "Index",
            field: "index",
            width: 150,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Plan Name",
            field: "name",
            width: 250,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Status",
            field: "state",
            width: 250,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Reference Dose",
            field: "referenceDose",
            width: 290,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Delivered Dose",
            field: "deliveredDose",
            width: 300,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Last Modified",
            field: "lastModified",
            width: 300,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Associated QA Plans",
            field: "qaPlanId",
            width: 300,
            titleAlign: "center",
            columnAlign: "center",
          }
        ],

        qaPlanColumns: [
          {
            width: 60,
            titleAlign: 'center',
            columnAlign: 'center',
            type: 'selection'
          },
          {
            field: "index",
            title: "Index",
            width: 150,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            field: "name",
            title: "Phantom Name",
            width: 240,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            field: "state",
            title: "QA Plan Name",
            width: 240,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            field: "referenceDose",
            title: "Fraction MU",
            width: 240,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            field: "state",
            title: "Status",
            width: 240,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            field: "deliveryTimeEstimate",
            title: "Delivery Time Estimate",
            width: 240,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            field: "lastTreatmentDate",
            title: "Last Treatment Date",
            width: 240,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            field: "lastFractionResult",
            title: "Last Fraction Result",
            width: 250,
            titleAlign: "center",
            columnAlign: "center",
          }
        ],
        qaPlansData: [],
      }
    },
    created: function () {
      self = this;
    },
    mounted: function () {
      self.refresh();
    },
    methods: {
      handleSortChange({ name, order }) {
        self.searchData = self.searchData.sort((a, b) =>
          order === "asc" ? a[name] - b[name] : b[name] - a[name]
        );
      },
      selectPatientChange: async function (selection, rowData) {
        if (selection.length > 1) {
          self.$alert("No more than one patient can be selected!");
          self.selectedPatient = [];
          self.treatmentPlansData = [];
          return;
        } else if (selection.length === 1) {
          let patient = selection[0];
          let key = patient.patientKey;
          let plans = await axios.axiosGet(api.getPatientPlanInfos, { key: key });
          let idx = 0;
          for (let plan of plans) {
            plan.index = ++idx;
          }
          self.treatmentPlansData = plans;
        } else {
          self.treatmentPlansData = [];
        }
      },
      setStyle(row, rowIndex) {
        return "color:green;font-size:18px;";
      },

      refresh: async function () {
        await self.getDbAllPatients();
        self.searchData = self.patientTableData;
      },
      //
      getDbAllPatients: async function () {
        let patients = await axios.axiosGet(api.getAllPatientInfos);
        let idx = 0;
        for (let patient of patients) {
          patient.index = ++idx;
          patient.birthDate = dateFormatOfDbPatient(patient.birthDate);
          patient.name = gbkToStringEncode(patient.name);
        }
        self.patientTableData = patients;
        console.log("");
      },

      txtChange: function () {
        self.searchData = [];
        for (let data of self.patientTableData) {
          if (
            data.name.toLowerCase().indexOf(self.condition.toLowerCase()) != -1 ||
            data.medicalId.toLowerCase().indexOf(self.condition.toLowerCase()) !=
            -1
          ) {
            self.searchData.push(data);
          }
        }

        if (self.condition === "") {
          self.searchData = self.patientTableData;
        }
      },

      //Treatment Plan row Treatment Plan Management
      rowDblClick: function () {
        if (self.selectedPlan.length === 0) {
          self.$alert("Please select a plan", "Warning")
          return;
        }

        self.$store.dispatch('savePlanInfo', self.selectedPlan);

        console.log(self.$store.getters.getPlanInfo);
        self.$router.push({
          name: "TreatmentPlanMng"
        });
      },

      selectChange(selection, rowData) {
        console.log('select-change', selection);
        if (selection.length == 1) {
          self.selectedPlan = selection[0];
        } else {
          self.selectedPlan = [];
        }
      },

      //  table 
      patientSortChange(params) {
        self.patientTableData = params;
      },
    }
  }
</script>

<style scoped>
  #planAdmin {
    background-color: #1b225b;
    width: 1920px;
    height: 1086px;
  }

  #containDiv {
    background-color: #cddde8;
    width: 1900px;
    height: 1020px;
    margin-left: 10px;
    margin-top: 10px;
  }

  .grid-content {
    width: 1900px;
    height: 340px;
    background: #fff;
  }

  .patientList {
    height: 340px;
  }

  .treatmentPlans {
    height: 340px;
    margin-top: 10px;
  }

  .qaPlans {
    height: 320px;
    margin-top: 10px;
  }

  .search {
    width: 250px;
    height: 20px;
    margin-left: 50px;
    font-size: 14px;
    font-weight: 400;
  }
</style>



