<template>
  <div>
    <v-table :height="200"
             :columns="qaPlanDcmSeriesTableColumn"
             :table-data="qaPlanDcmSeriesData"
             row-hover-color="#ee9"
             row-click-color="#F0E0FF"
             title-bg-color="#BBB8BF"
             odd-bg-color="#e0f7fa"
             even-bg-color="#b2dfdb">
    </v-table>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        qaPlanDcmSeriesTableColumn: [
          {
            title: "Primary",
            field: "primary",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Modality",
            field: "modality",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Phase",
            field: "phase",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Series Description",
            field: "seriesDescription",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Series UID",
            field: "seriesUID",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Study UID",
            field: "studyUID",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Scan Date(YYYY-mm-dd HH:mm:ss)",
            field: "scanDate",
            width: 271,
            titleAlign: "center",
            columnAlign: "center",
          }
        ],
        qaPlanDcmSeriesData: [],
      }
    }
  }
</script>

<style scoped>

</style>



