<template>
  <div class="planDetails" style="width:1800px;height:800px">
    <el-row>
      <el-col :span="12">
        <div class="details">
          <h3 style="padding-left:10px; text-align:left;">Plan Overview</h3>
          <mu-form :model="planOverviewForm" class="planOverviewForm" label-position="top" label-width="150px" style="margin:10px">
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="planName" label="Plan Name" style="text-align:left;">
                  <input v-model="planOverviewForm.planName" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="treatmentSiteName" label="Treatment Site Name" style="text-align:left;">
                  <input v-model="planOverviewForm.treatmentSiteName" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="status" label="Status" style="text-align:left;">
                  <input v-model="planOverviewForm.status" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="pathologyName" label="Pathology Name" style="text-align:left;">
                  <input v-model="planOverviewForm.pathologyName" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="dRRGenerationStatus" label="DRR Generation Status" style="text-align:left;">
                  <input v-model="planOverviewForm.dRRGenerationStatus" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="planConsistent" label="Plan Consistent" style="text-align:left;">
                  <input v-model="planOverviewForm.planConsistent" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="lastModifiedDate" label="Last Modified Date" style="text-align:left;">
                  <input v-model="planOverviewForm.lastModifiedDate" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
          </mu-form>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="details1">
          <h3 style="padding-left:10px; text-align:left;">Prescription Details</h3>
          <mu-form :model="prescriptionDetailsForm" class="prescriptionDetailsForm" label-position="top" label-width="250px" style="margin:10px">
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="prescribedPlanDose" label="Prescribed Plan Dose (cGy)" style="text-align:left;">
                  <input v-model="prescriptionDetailsForm.prescribedPlanDose" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="referenceDosePerFraction" label="Reference Dose Per Fraction (cGy)" style="text-align:left;">
                  <input v-model="prescriptionDetailsForm.referenceDosePerFraction" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="isodoseLine" label="% Isodose Line" style="text-align:left;">
                  <input v-model="prescriptionDetailsForm.isodoseLine" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="fractionMU" label="Fraction MU" style="text-align:left;">
                  <input v-model="prescriptionDetailsForm.fractionMU" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="maximumDose" label="Maximum Dose (cGy)" style="text-align:left;">
                  <input v-model="prescriptionDetailsForm.maximumDose" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="doseCalculationAlgorithm" label="Dose Calculation Algorithm" style="text-align:left;">
                  <input v-model="prescriptionDetailsForm.doseCalculationAlgorithm" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="planMU" label="Plan MU" style="text-align:left;">
                  <input v-model="prescriptionDetailsForm.planMU" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="multiPlanVersion" label="MultiPlan Version" style="text-align:left;">
                  <input v-model="prescriptionDetailsForm.multiPlanVersion" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="referenceDose" label="Reference Dose (cGy)" style="text-align:left;">
                  <input v-model="prescriptionDetailsForm.referenceDose" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="multiPlanLastModifiedDate" label="MultiPlan Last Modified Date" style="text-align: left;">
                  <input v-model="prescriptionDetailsForm.multiPlanLastModifiedDate" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
          </mu-form>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="12">
        <div class="details2">
          <h3 style="padding-left:10px; text-align:left;">Treatment Parameters</h3>
          <mu-form :model="treatmentParametersForm" class="treatmentParametersForm" label-position="top" label-width="250px" style="margin:10px">
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="numberOfPrescribedFractions" label="Number of Prescribed Fractions" style="text-align:left;">
                  <input v-model="treatmentParametersForm.numberOfPrescribedFractions" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="patientPosition" label="Patient Position" style="text-align:left;">
                  <input v-model="treatmentParametersForm.patientPosition" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="targetSCCSystem" label="Target SCC System" style="text-align:left;">
                  <input v-model="treatmentParametersForm.targetSCCSystem" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="trackingMethod" label="Tracking Method" style="text-align:left;">
                  <input v-model="treatmentParametersForm.trackingMethod" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="sccSystemVersion" label="SCC System Version" style="text-align:left;">
                  <input v-model="treatmentParametersForm.sccSystemVersion" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="synchrony" label="Synchrony" style="text-align:left;">
                  <input v-model="treatmentParametersForm.synchrony" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="anatomy" label="Anatomy" style="text-align:left;">
                  <input v-model="treatmentParametersForm.anatomy" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="inTempo" label="InTempo" style="text-align:left;">
                  <input v-model="treatmentParametersForm.inTempo" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
            <el-row>
              <el-col :span="8">
                <mu-form-item prop="pathSet" label="Path Set" style="text-align:left;">
                  <input v-model="treatmentParametersForm.pathSet" disabled />
                </mu-form-item>
              </el-col>
              <el-col :span="8" style="margin-left:200px">
                <mu-form-item prop="collimatorType" label="Collimator Type" style="text-align:left;">
                  <input v-model="treatmentParametersForm.collimatorType" disabled />
                </mu-form-item>
              </el-col>
            </el-row>
          </mu-form>
        </div>
      </el-col>
      <el-col :span="12">
        <el-col>
          <div class="details3">
            <h3 style="padding-left:10px; text-align:left;">Delivery Details</h3>
            <mu-form :model="deliveryDetailsForm" class="deliveryDetailsForm" label-position="top" label-width="250px" style="margin:10px">
              <el-row>
                <el-col :span="8">
                  <mu-form-item prop="deliveredPlanDose" label="Delivered Plan Dose (cGy)" style="text-align:left;">
                    <input v-model="deliveryDetailsForm.deliveredPlanDose" disabled />
                  </mu-form-item>
                </el-col>
                <el-col :span="8" style="margin-left:200px">
                  <mu-form-item prop="deliveredPlanMU" label="Delivered Plan MU" style="text-align:left;">
                    <input v-model="deliveryDetailsForm.deliveredPlanMU" disabled />
                  </mu-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="8">
                  <mu-form-item prop="lastTreatmentDate" label="Last Treatment Date" style="text-align:left;">
                    <input v-model="deliveryDetailsForm.lastTreatmentDate" disabled />
                  </mu-form-item>
                </el-col>
              </el-row>
            </mu-form>
          </div>
        </el-col>
        <el-col>
          <div class="details4">
            <h3 style="padding-left:10px; text-align:left;">Specialists</h3>
            <mu-form :model="specialistsForm" class="specialistsForm" label-position="top" label-width="250px" style="margin:10px">
              <el-row>
                <el-col :span="8">
                  <mu-form-item prop="physicist" label="Physicist" style="text-align:left;">
                    <input v-model="specialistsForm.physicist" disabled />
                  </mu-form-item>
                </el-col>
                <el-col :span="8" style="margin-left:200px">
                  <mu-form-item prop="surgeon" label="Surgeon" style="text-align:left;">
                    <input v-model="specialistsForm.surgeon" disabled />
                  </mu-form-item>
                </el-col>
              </el-row>
              <el-row>
                <el-col :span="8">
                  <mu-form-item prop="radiationOncologist" label="Radiation Oncologist" style="text-align:left;">
                    <input v-model="specialistsForm.radiationOncologist" disabled />
                  </mu-form-item>
                </el-col>
                <el-col :span="8" style="margin-left:200px">
                  <mu-form-item prop="referringPhysician" label="Referring Physician" style="text-align:left;">
                    <input v-model="specialistsForm.referringPhysician" disabled />
                  </mu-form-item>
                </el-col>
              </el-row>
            </mu-form>
          </div>
        </el-col>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  export default {
    data() {
      return {
        pagename: 'Treatment Plan Management',
        pageIndex: 0,
        planOverviewForm: {
          planName: '',
          treatmentSiteName: '',
          status: '',
          pathologyName: '',
          dRRGenerationStatus: '',
          planConsistent: '',
          lastModifiedDate: '',
        },
        prescriptionDetailsForm: {
          numberOfPrescribedFractions: '',
          patientPosition: '',
          isodoseLine: '',
          fractionMU: '',
          maximumDose: '',
          doseCalculationAlgorithm: '',
          planMU: '',
          multiPlanVersion: '',
          referenceDose: '',
          multiPlanLastModifiedDate: ''
        },
        treatmentParametersForm: {
          numberOfPrescribedFractions: '',
          patientPosition: '',
          targetSCCSystem: '',
          trackingMethod: '',
          sccSystemVersion: '',
          synchrony: '',
          anatomy: '',
          inTempo: '',
          pathSet: '',
          collimatorType: '',
        },
        deliveryDetailsForm: {
          deliveredPlanDose: '',
          deliveredPlanMU: '',
          lastTreatmentDate: '',
        },
        specialistsForm: {
          physicist: '',
          surgeon: '',
          radiationOncologist: '',
          referringPhysician: '',
        }
      }
    },
    created() {
      self = this;
    },
    mounted() {
      self.planOverviewForm.planName = self.planInfo.name;
      self.planOverviewForm.status = self.planInfo.state;
    },
    computed: {
      ...mapState({
        planInfo: state => state.planInfo,
      })
    }
  }
</script>

<style lang="less" scoped>

  .planDetails {
    margin-left: 10px;
    margin-top: 10px;
  }

  .details, .details1, .details2, .details3, .details4 {
    width: 935px;
    height: 480px;
    background-color: #B1BCF6;
  }

  .details1 {
    margin-left: 45px;
  }

  .details2 {
    height: 460px;
    margin-top: 10px;
  }

  .details3, .details4 {
    height: 225px;
    margin-top: 10px;
    margin-left: 45px;
  }

  input {
    border: 1px solid #ccc;
    padding: 7px 0px;
    border-radius: 3px;
    padding-left: 5px;
    font-size: 16px;
    height: 25px;
    width: 300px;
    -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
    -webkit-transition: border-color ease-in-out 0.15s, -webkit-box-shadow ease-in-out 0.15s;
    -moz-transition: border-color ease-in-out 0.15s, -moz-box-shadow ease-in-out 0.15s;
    -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
    transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  }
</style>



