import Vue from "vue";
import Router from "vue-router";
import store from '../store'
import LoginVue from "../views/Login/Login.vue";
import MainView from "../views/Main/Main.vue";
import Pages from "../views/Layout/PagesView.vue";

import PatientAdmin from "../views/PatientAdmin/PatientAdmin.vue";
import ImportPatient from "../views/PatientAdmin/ImportPatient.vue";
import EditPatient from "../views/PatientAdmin/EditPatient.vue";

import PlanAdmin from "../views/PlanAdmin/PlanAdmin.vue";
import TreatmentPlanMng from "../views/PlanAdmin/TreatmentPlanManagement.vue";
import PlanDetails from "../views/PlanAdmin/PlanDetails.vue";
import FractionMng from "../views/PlanAdmin/FractionMng.vue";
import QAPlans from "../views/PlanAdmin/QAPlans.vue";

import UserAdmin from "../views/UserAdmin/UserAdmin.vue";
import AddUser from "../views/UserAdmin/AddUser.vue";
import EditUser from "../views/UserAdmin/EditUser.vue";

Vue.use(Router);

const routes = [
  {
    path: '/',
    redirect: '/webApp/'
  },
  {
    path: '/webApp',
    name: 'loginVue',
    component: LoginVue,
  },
  {
    path: '/webApp/main',
    name: 'MainView',
    component: MainView,
  },
  {
    path: '/patient/',
    component: Pages,
    children:[
      {
        path: 'patientadmin',
        name: 'PatientAdmin',
        component: PatientAdmin
      },
      {
        path: 'importpatient',
        name: 'Importpatient',
        component: ImportPatient
      },
      {
        path: 'editpatient',
        name: 'EditPatient',
        component: EditPatient
      }
    ]
  },
  {
    path: '/plan/',
    component: Pages,
    children: [
      {
        path: 'planadmin',
        name: 'PlanAdmin',
        component: PlanAdmin
      },
      {
        path: 'treatmentplanmng',
        name: 'TreatmentPlanMng',
        component: TreatmentPlanMng,
        redirect: { name: 'PlanDetails' },
        children: [
          {
            path: 'planDetails',
            name: 'PlanDetails',
            component: PlanDetails
          },
          {
            path: 'fractionmng',
            name: 'FractionMng',
            component: FractionMng
          },
          {
            path: 'qaPlans',
            name: 'QAPlans',
            component: QAPlans
          }
        ]
      },
    ]
  },
  {
    path: '/user/',
    component: Pages,
    children:[
      {
        path: 'useradmin',
        name: 'UserAdmin',
        component: UserAdmin
      },
      {
        path: 'addUser',
        name: 'AddUser',
        component: AddUser
      },
      {
        path: 'editUser',
        name: 'EditUser',
        component: EditUser
      },
    ]
  },
]

const router = new Router({
  mode: "history",
  //mode:'hash',
  base: process.env.BASE_URL,
  routes
});

router.beforeEach((to, from, next) => {
  // if user not exist, then go to login
  if (to.name !== 'loginVue') {
    let user = store.getters.user
    if (user === null || user.name === null || user.name === undefined) {
      router.replace({name: 'loginVue'})
    }
  }
  next()
})

export default router;



