import {
  //Module
  SET_PAGENAME,

  //Patient
  UPDATE_PATIENT,

  //Plan
  SET_PLANINFO,

  //User
  SET_USER,
  SET_LOGINDATE,

} from './mutation-types'


export default{
  //Module
  setPageName({commit}, pageName){
    commit(SET_PAGENAME, pageName);
  },

  //Patient
  savePatient({commit}, patient) {
    commit(UPDATE_PATIENT, patient);
  },

  //Plan
  savePlanInfo({commit}, plan){
    commit(SET_PLANINFO,plan);
  },

  //User
  setUser({commit}, user){
    commit(SET_USER, user);
  },

  setLoginDate({commit}, date){
    commit(SET_LOGINDATE, date)
  },
}


