<template>
  <div>
    <el-row>
      <el-col :span="12">
        <h3>Phantom Record Information</h3>
        <mu-form class="phantomRecordInfoForm" :model="phantomRecordInfoData" label-position="top" label-width="250px">
          <el-row>
            <el-col :span="12">
              <mu-form-item prop="medicalID" label="Medical ID" style="text-align:left; margin-left:40px;">
                <input v-model="phantomRecordInfoData.medicalID" disabled />
              </mu-form-item>
              <mu-form-item prop="type" label="Type" style="text-align:left; margin-left:40px;">
                <input v-model="phantomRecordInfoData.type" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="12">
              <mu-form-item prop="name" label="Name" style="text-align:left;">
                <input v-model="phantomRecordInfoData.name" disabled />
              </mu-form-item>
            </el-col>
          </el-row>
        </mu-form>
      </el-col>
      <el-col :span="12">
        <h3>Reference Patient Plan</h3>
        <el-row>
          <mu-form class="referencePatientPlan" :model="referencePatientPlanData" label-position="top" label-width="250px">
            <el-col :span="12">
              <mu-form-item prop="planName" label="Plan Name" style="text-align:left;">
                <input v-model="referencePatientPlanData.planName" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="12">
              <mu-form-item prop="fractionMU" label="Fraction MU" style="text-align:left;">
                <input v-model="referencePatientPlanData.fractionMU" disabled />
              </mu-form-item>
            </el-col>
          </mu-form>
        </el-row>
      </el-col>
    </el-row>
    <el-row>
      <h3>QA Plan Information</h3>
      <mu-form class="qaPlanInfoForm" :model="qaPlanInfoData" label-position="top" label-width="250px">
        <el-col :span="12">
          <el-row>
            <el-col :span="12">
              <mu-form-item prop="planName" label="Plan Name" style="text-align:left; margin-left:40px;">
                <input v-model="qaPlanInfoData.planName" disabled />
              </mu-form-item>
              <mu-form-item prop="templatePlanName" label="Template Plan Name" style="text-align:left; margin-left:40px;">
                <input v-model="qaPlanInfoData.templatePlanName" disabled />
              </mu-form-item>
              <mu-form-item prop="status" label="Status" style="text-align:left; margin-left:40px;">
                <input v-model="qaPlanInfoData.status" disabled />
              </mu-form-item>
              <mu-form-item prop="trackingMethod" label="Tracking Method" style="text-align:left; margin-left:40px;">
                <input v-model="qaPlanInfoData.trackingMethod" disabled />
              </mu-form-item>
              <mu-form-item prop="planConsistent" label="Plan Consistent" style="text-align:left; margin-left:40px;">
                <input v-model="qaPlanInfoData.planConsistent" disabled />
              </mu-form-item>
            </el-col>
            <el-col :span="12">
              <mu-form-item prop="drrGenerationStatus" label="DRR Generation Status" style="text-align:left;">
                <input v-model="qaPlanInfoData.drrGenerationStatus" disabled />
              </mu-form-item>
              <mu-form-item prop="doseScaleFactor" label="Dose Scale Factor" style="text-align:left;">
                <input v-model="qaPlanInfoData.doseScaleFactor" disabled />
              </mu-form-item>
              <mu-form-item prop="fractionMU" label="Fraction MU" style="text-align:left;">
                <input v-model="qaPlanInfoData.fractionMU" disabled />
              </mu-form-item>
              <mu-form-item prop="numOfModifiedOrZeroBeams" label="Number of Modified or Zero Beams" style="text-align:left;">
                <input v-model="qaPlanInfoData.numOfModifiedOrZeroBeams" disabled />
              </mu-form-item>
            </el-col>
          </el-row>
        </el-col>
      </mu-form>
    </el-row>
  </div>
</template>

<script>
  import * as api from '../http/api/api';
  import axios from '../http/index';
  let protoRoot = require('@/proto/proto')
  let protobuf = require("protobufjs")
  export default {
    data() {
      return {
        phantomRecordInfoData: {
          medicalID: '',
          name: '',
          type: ''
        },
        referencePatientPlanData: {
          planName: '',
          fractionMU: '',
        },
        qaPlanInfoData: {
          planName: '123',
          templatePlanName: '',
          status: '',
          trackingMethod: '',
          planConsistent: '',
          drrGenerationStatus: '',
          doseScaleFactor: '',
          fractionMU: '',
          numOfModifiedOrZeroBeams: '',
        }
      }
    },
    created: function () {
      self = this;
    },
    mounted: function () {
      self.getfile();      
    },
    methods: {
      getfile: async function () {
        let planInfo = protoRoot.lookupType("model.data.PlanInfo");
        console.log(planInfo);
        let data = {
          planKey: '27997106-E3C2-49B0-A4BC-5B1F3869D2EC',
          patientKey: '86B88DA4-85E7-46C7-B3CB-687D35EB12C6',
          name: 'final_plan',
          state: 1,
          filePath: '123456',
          config_version: '0.1',
        };
        let errMsg = planInfo.verify(data);
        if (errMsg) throw errMsg;
        // Obtain a message type
        let message = planInfo.create(data);
        console.log(`message = ${JSON.stringify(message)}`);
        let buffer = planInfo.encode(message).finish();
        console.log(`buffer = ${buffer}`);
        console.log(`buffer = ${JSON.stringify(planInfo.decode(buffer))}`);
        let obj = planInfo.toObject(message);
        console.log(obj);

        let fileData = await axios.axiosGet(api.getFileRaw, { key: 'd02742a7-58d0-483d-81e0-2fad0e1aa508' })
        console.log(fileData);

      }
    }
  }
</script>

<style scoped>
  h3 {
    text-align:left;
  }

  input {
  border: 1px solid #ccc;
  padding: 7px 0px;
  border-radius: 3px;
  padding-left: 5px;
  font-size: 16px;
  height: 25px;
  width: 300px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -webkit-transition: border-color ease-in-out 0.15s,
    -webkit-box-shadow ease-in-out 0.15s;
  -moz-transition: border-color ease-in-out 0.15s,
    -moz-box-shadow ease-in-out 0.15s;
  -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}

  .phantomRecordInfoForm /deep/ .mu-form-item{
      margin-bottom:-10px;
  }

  .qaPlanInfoForm /deep/ .mu-form-item{
      margin-bottom:-10px;
  }
</style>



