<template>
  <div>
    <div class="tableDiv">
      <v-table :height="200"
               :columns="fractionTableColumn"
               :table-data="fractionListData"
               row-hover-color="#ee9"
               row-click-color="#F0E0FF"
               title-bg-color="#BBB8BF"
               odd-bg-color="#e0f7fa"
               even-bg-color="#b2dfdb">
      </v-table>
    </div>
    <div class="detailsDiv">
      <h3>QA Plan Fraction Details</h3>
      <mu-form class="fractionDetailsForm" :model="fractionDetailsData" label-position="top" label-width="250px">
        <el-row>
          <el-col :span="8">
            <mu-form-item prop="fractionName" label="Fraction Name" style="text-align:left; margin-left:40px;">
              <input v-model="fractionDetailsData.fractionName" disabled />
            </mu-form-item>
            <mu-form-item prop="status" label="Status" style="text-align:left; margin-left:40px;">
              <input v-model="fractionDetailsData.status" disabled />
            </mu-form-item>
            <mu-form-item prop="deliveredMU" label="Delivered MU" style="text-align:left; margin-left:40px;">
              <input v-model="fractionDetailsData.deliveredMU" disabled />
            </mu-form-item>
            <mu-form-item prop="analysisResult" label="Analysis Result" style="text-align:left; margin-left:40px;">
              <input v-model="fractionDetailsData.analysisResult" disabled />
            </mu-form-item>
          </el-col>
          <el-col :span="16">
            <mu-form-item prop="notes" label="Notes" style="text-align:left; margin-left:40px;">
              <textarea style="width:640px;height:100px" v-model="fractionDetailsData.notes" disabled />
            </mu-form-item>
          </el-col>
        </el-row>
      </mu-form>
    </div>
    <div>
      <h3>Analysis file</h3>

    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        fractionListData: [],
        fractionTableColumn: [
          {
            title: "Fraction Name",
            field: "fractionName",
            width: 316,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Status",
            field: "status",
            width: 316,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Delivered MU",
            field: "deliveredMU",
            width: 316,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Last Treatment Date(YYYY-mm-dd)",
            field: "lastTreatmentDate",
            width: 316,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Analysis Result",
            field: "analysisResult",
            width: 316,
            titleAlign: "center",
            columnAlign: "center",
          },
          {
            title: "Analysis Result",
            field: "analysisResult",
            width: 320,
            titleAlign: "center",
            columnAlign: "center",
          },
        ],
        fractionDetailsData: {
          fractionName: '',
          status: '',
          deliveredMU: '',
          analysisResult: '',
          notes: '',
        }
      }
    }

  }
</script>

<style scoped>
  .tableDiv {
    width: 100%;
    height: 200px;
  }
  .detailsDiv {
    width: 100%;
    height: 300px;
  }
  h3 {
    text-align:left;
    padding-left:10px;
  }

  input {
  border: 1px solid #ccc;
  padding: 7px 0px;
  border-radius: 3px;
  padding-left: 5px;
  font-size: 16px;
  height: 25px;
  width: 300px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -moz-box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  box-shadow: inset 0 1px 1px rgba(0, 0, 0, 0.075);
  -webkit-transition: border-color ease-in-out 0.15s,
    -webkit-box-shadow ease-in-out 0.15s;
  -moz-transition: border-color ease-in-out 0.15s,
    -moz-box-shadow ease-in-out 0.15s;
  -o-transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
  transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}

  .fractionDetailsForm /deep/ .mu-form-item{
      margin-bottom:-10px;
  }
</style>



