<template>
  <div id="Contain">
    <div id="bgDiv" />
    <div id="transparentDiv" />
    <div id="login">
      <div id="welcomeDiv">
        <div id="loginTxtDiv">Login</div>
        <div id="welcomeTxtDiv">Welcome to SCC</div>
      </div>
      <div id="inputDiv">
        <div id="logoImg" />
        <el-input class="username" v-model="username" placeholder=""></el-input>
        <el-input class="pwd" v-model="password" placeholder="" show-password></el-input>
        <el-button class="loginbutton" type="primary" @click="loginClick"></el-button>
      </div>
    </div>
    <!-- <div class="copyright">&copy;  2020</div> -->
  </div>
</template>

<script>
  import * as api from '../../http/api/api';
  import axios from '../../http/index';
  import { mapActions } from 'vuex'
  export default {
    data() {
      return {
        username: "",
        password: ""
      };
    },
    created: function () {
      self = this;
    },
    methods: {
      loginClick: async function () {
        if (self.username !== "" && self.password !== "") {
          let getUserName = "";
          let usersId = await axios.axiosGet(api.getAllUserKeys) //await
          for (let id of usersId) {
            let userInfo = await axios.axiosGet(api.getUserInfo, { key: id })
            if (userInfo.name === self.username) {
              getUserName = userInfo.name;
              self.setUser(userInfo);
              let date = new Date();
              self.setLoginDate(date);
              self.$router.push({
                name: "MainView"
              });
              break;
            }
          }

          if (getUserName === "") {
            self.$alert("");
          }
        } else {
          self.$alert("", "LoginMsg", "Warning");
        }
      },
      ...mapActions([
        'setUser',
        'setLoginDate',
      ])
    }
  };
</script>

<style scoped>
    #Contain {
      width: auto;
      height: 1086px;
      background-color: #bbb8b8;
    }

    #bgDiv,
    #transparentDiv,
    #login {
      width: 1920px;
      height: 100%;
      top: 0;
    }

    #bgDiv {
      background: url( ../../assets/igps.png) no-repeat center center fixed;
      background-size: cover;
    }

    #transparentDiv {
      background: #bbb8b8;
      position: absolute;
      opacity: 0.9;
    }

    #login {
      position: absolute;
      width: 640px;
      height: 400px;
      margin-left: 33%;
      margin-top: 15%;
    }

    #welcomeDiv,
    #inputDiv {
      border-radius: 10px;
      box-shadow: 10px 10px 10px #797979;
    }

    #welcomeDiv {
      position: absolute;
      background-color: #f2f2f2;
      width: 60%;
      height: 65%;
      margin: 8% -5%;
    }

    #inputDiv {
      position: absolute;
      background-color: #7f7f7f;
      width: 75%;
      height: 90%;
      margin: 0 35%;
    }

    #loginTxtDiv {
      position: relative;
      margin-left: 60px;
      margin-top: 40px;
      width: 100px;
      font-size: 40px;
    }

    #welcomeTxtDiv {
      width: 200px;
      margin-top: 50px;
      font-size: 25px;
      color: #918f8f;
      margin-left: 30px;
      font-style: oblique;
    }

    #logoImg {
      width: 160px;
      height: 30px;
      background: url(../../assets/rayerLogo.png) no-repeat;
      background-size: cover;
      margin: 10px 0 0 10px;
    }

    .username,
    .pwd,
    .loginbutton {
      float: left;
      width: 250px;
      font-size: 16px;
      margin-left: 120px;
    }

    .pwd {
      margin-top: 10px;
    }

    .username {
      margin-top: 30px;
    }

    .loginbutton {
      margin-top: 50px;
      height: 40px;
      font-size: 20px;
    }

    /* .copyright {
      position: relative;
  } */
</style>



